.youtubeCard {
  width: 300px;
  transition: all 0.5s;
  padding: 1vmax;
  margin: 2vmax;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}
.democertificate::after {
  content: "VIEW CERTIFICATE" !important;
  width: 100%;
  height: 25% !important;
  font: 100 2rem "Roboto";
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: hsl(250, 100%, 75%);
  color: white;
}
.youtubeCard > a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.712);
  width: inherit;
}

.youtubeCard img {
  width: inherit;
}

.youtubeCard:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.329);
}

@media screen and (max-width: 600px) {
  .youtubeCard {
    width: 200px;
  }
  .democertificate::after {
    content: "VIEW CERTIFICATE" !important;
    font: 60 1.3rem "Roboto" !important;
   
  }
}
